%;\n}\n\n.avatar-uploader :deep(.el-upload) {\n  border: 1px dashed var(--el-border-color);\n  border-radius: 6px;\n  cursor: pointer;\n  position: relative;\n  overflow: hidden;\n  transition: var(--el-transition-duration-fast);\n\n  img {\n    width: 80px;\n    height: 80px;\n    object-fit: cover;\n  }\n\n  &:hover {\n    border-color: var(--el-color-primary);\n  }\n\n  .el-icon.avatar-uploader-icon {\n    font-size: 28px;\n    color: #8c939d;\n    width: 80px;\n    height: 80px;\n    text-align: center;\n  }\n}\n\n.color-title {\n  font-size: 12px;\n  color: #333333;\n  font-weight: 400;\n  margin-right: 8px;\n}\n\n.color-block-box {\n  padding: 0 32px;\n  height: 30px;\n  display: flex;\n  flex-wrap: wrap;\n  /* 允许换行 */\n\n  .color-block {\n    cursor: pointer;\n    color: #666;\n    border-radius: 4px;\n    width: 20px;\n    height: 20px;\n    transition: transform 0.2s ease;\n\n    div {\n      cursor: pointer;\n      text-align: center;\n      line-height: 20px;\n    }\n\n    &:not(:nth-child(4n)) {\n      margin: 0 26px 20px 0;\n    }\n  }\n\n  .action-color-block {\n    position: relative;\n    transform: rotate(45deg);\n    /* 旋转45度，变成菱形 */\n\n    /* 使用伪元素创建一个透明的占位元素，使菱形保持正常占据空间 */\n    &::before {\n      width: 20px;\n      height: 20px;\n      display: block;\n      padding-bottom: 100%;\n      /* 与菱形相同的高度 */\n    }\n  }\n\n  .action-color-block-font {\n    position: absolute;\n    top: 50%;\n    /* 垂直居中 */\n    left: 50%;\n    /* 水平居中 */\n    transform: translate(-50%, -50%) rotate(-45deg);\n    /* 反向旋转，抵消外部菱形的旋转 */\n  }\n}\n\n:deep() .vc-color-wrap {\n  width: 66px;\n  height: 20px;\n}\n\n.action-theme-box {\n  display: flex;\n\n  > div:first-child {\n    margin: 0 20px;\n    margin-right: 0px;\n  }\n}\n\n.border-color {\n  border: 1px #666 solid;\n}\n\n.tabs-type {\n  display: flex;\n  justify-content: space-between;\n  padding: 0 20px;\n  // padding-right: 0px;\n  margin-bottom: 16px;\n\n  > div {\n    border: 1px solid #cccccc;\n    border-radius: 4px;\n    position: relative;\n\n    > .is-action-icon {\n      width: 16px;\n      height: 16px;\n      position: absolute;\n      right: 0;\n      bottom: 0;\n    }\n  }\n\n  .is-action {\n    border: 1px solid #4b82fa;\n  }\n}\n\n.new-year {\n  background: url(\"@/assets/h5/newYear.png\") 100% 100% no-repeat;\n  margin: 0 30px 0 20px;\n  position: relative;\n  height: 60px;\n\n  :deep() .el-checkbox {\n    position: absolute;\n    padding: 0 16px;\n    width: 66px;\n    height: 26px;\n    background: #ffffff;\n    border-radius: 13px;\n    right: 30px;\n    top: 22px;\n\n    .el-checkbox__inner {\n      width: 12px;\n      height: 12px;\n      border-color: #ff4421;\n\n      &::after {\n        top: 0;\n        left: 3px;\n      }\n    }\n\n    .el-checkbox__input.is-checked .el-checkbox__inner {\n      background-color: #ff4421 !important;\n      color: #ff4421 !important;\n    }\n\n    .el-checkbox__label {\n      color: #ff4421;\n    }\n  }\n}\n"],"sourceRoot":""}]);
